<template>
  <div class="page ma-2">
    <v-layout row wrap>
        <v-flex xs12 class="ma-2">
          <v-card>
            <v-card-title primary-title>
              <div>{{curWallet}}</div>
            </v-card-title>
          </v-card>
        </v-flex>

      <v-flex xs12 class="ma-2">
        <v-card>
          <v-card-title primary-title>
            <div>({{collects.length}})</div>
          </v-card-title>
          <v-card-text>
            <v-layout row wrap>
              <v-flex xs4 v-for="(item,index) in collects" :key="index" class="pa-1">
                <coupon
                  :title="item.title"
                  :money="item.money"
                  :author="item.author"
                  :belong="item.belong"
                  :status="item.status"
                  :index="item.index"
                ></coupon>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>

       <v-flex xs12 class="ma-2">
        <v-card>
          <v-card-title primary-title>
            <div>({{created.length}})</div>
          </v-card-title>
          <v-card-text>
            <v-layout row wrap>
              <v-flex xs4 v-for="(item,index) in created" :key="index" class="pa-1">
                <coupon
                  :title="item.title"
                  :money="item.money"
                  :author="item.author"
                  :belong="item.belong"
                  :status="item.status"
                  :index="item.index"
                ></coupon>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>



    </v-layout>
  </div>
</template>

<script>
import {mapState } from 'vuex'
import Coupon from '@/components/Coupon'
export default {
  data(){
    return {

    }
  },
  computed: {
    ...mapState(['curWallet','data']),
    created(){
      if(this.data){
       let result = []
       for(let i=0,n=this.data.length;i<n;i++){
        result.push(Object.assign({}, this.data[i], {index: i}))
       }
       return result.filter(item=> item.author===this.curWallet)
      }
      return []
    },
    collects(){
      if(this.data){
       let result = []
       for(let i=0,n=this.data.length;i<n;i++){
        result.push(Object.assign({}, this.data[i], {index: i}))
       }
       return result.filter(item => item.belong === this.curWallet)
      }
      return []
    }

  },
  components: {
    Coupon,
  }
}
</script>

<style>

</style>



