import NebPay from './dist/nebpay'
// var NebPay = require("./dist/nebpay");
const nebPay = new NebPay()

//TODO
export const DAPP_ADDRESS = 'n225iPvGVxUizKBKST4d3fPGtrsQQuaoqYp';//main
//hash ae5c702a08ef03741786bd5f4472b8c9c812a5ff531ed8058757ab6f5ca7315a
export const CREATOR = 'n1KJ1924CdCKkzSD8Nrk9CH52Z9xnSvtRCf';
/**
 *
 * @param {String} callFunction 
 * @param {Array} param 
 * @param {Boolean} needPay 
 */
export default function (callFunction, param = [], needPay = false) {
  return new Promise((resolve, reject) => {
    const to = DAPP_ADDRESS
    const value = '0'
    const callArgs = JSON.stringify(param)
    /**
     *    nebPay  simulateCall 
     *    nebPay  call 
     */
    const way = needPay ? 'call' : 'simulateCall'
    nebPay[way](to, value, callFunction, callArgs, {
      listener: (res) => {
        console.log('listener', res)
        try {
          resolve(JSON.parse(res.result))
        } catch (e) {
          reject(res.result)
        }
      }
    })
  })
}

var Nebulas = require('nebulas')
var Neb = Nebulas.Neb; // Neb
var neb = new Neb(new Nebulas.HttpRequest("https://mainnet.nebulas.io"));//https://testnet.nebulas.io
var Account = Nebulas.Account;

export function getAll(city){
  var value = "0";
  var nonce = "0"
  var gas_price = "1000000"
  var gas_limit = "2000000"
  var callFunction = "getAll";
  var callArgs = "[\"" + city+ "\"]"; //in the form of ["args"]
  var contract = {
      "function": callFunction,
      "args": callArgs
  }
  return neb.api.call(CREATOR,DAPP_ADDRESS,value,nonce,gas_price,gas_limit,contract)
}

export function callContract(method,args){
  return new Promise((resolve,reject)=>{
    nebPay.call(DAPP_ADDRESS,"0",method,JSON.stringify(args),{
      listener: (res) => {
        console.log('listener', res)
        try {
          resolve(res)
        } catch (e) {
          reject(res)
        }
      }});
  });
}


