<template>
  <v-container fluid>
   <v-layout>
      <v-flex xs2>
         <v-select
          :items="cities"
          v-model="cityModel"
          single-line
          @change="doSearch"
        ></v-select>
      </v-flex>
      <v-flex xs10>
       <v-text-field append-icon="search" 
          hide-details single-line 
          v-model="searchTxt"
          :autofocus="true"
          :loading="loading"></v-text-field>
      </v-flex>
   </v-layout>

  <v-layout row wrap v-if="coupons && coupons.length >0">
    <v-flex xs4 v-for="(item,index) in coupons" :key="index" class="pa-2">
      <coupon
        :title="item.title"
        :money="item.money"
        :author="item.author"
        :belong="item.belong"
        :status="item.status"
        :index="item.index"
      ></coupon>
    </v-flex>
  </v-layout>

  <div class="ma-2 pa-2" v-else>
    <div class="working" v-if="loading">...</div>
    <div v-if="success">...</div>
    <div v-if="fail">...</div>
  </div>

  </v-container>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Coupon from '../components/Coupon.vue'
import {getAll} from '@/api'
export default {
  data(){
    return {
      loading: false,
      cityModel: null,
      searchTxt:'',
      success: false,
      fail: false,
    }
  },
  computed: {
    ...mapState(['data','cities','city']),
   coupons(){
     if(this.data){
       let result = []
       for(let i=0,n=this.data.length;i<n;i++){
        result.push(Object.assign({}, this.data[i], {index: i}))
       }
       if(this.searchTxt!=null && this.searchTxt.length > 0){
        return result.filter(item=> item.title.indexOf(this.searchTxt) >= 0)
       }
       return result
     }
     return []
   }
  },
  created () {
    this.cityModel = this.city
    this.doSearch()
  },
  methods: {
    doSearch(value){
      this.$store.commit('changeCity',value);
      if(this.loading)return;
      this.loading = true;
      this.$store.commit('searchData',[]);
      this.searchTxt = '';
      this.success = false
      getAll(this.city)
        .then(response=>{
          console.log('---response.data--' + JSON.stringify(response))
          if (response["result"] !== "null") {
             this.$store.commit('searchData', JSON.parse(response["result"]))
           }else{
            this.success = true
           }
           setTimeout(()=>{
             this.loading = false
             this.success = false
           },2000)
        })
        .catch(err=>{
          this.fail = true
          console.error(err)
          alert('!');
          setTimeout(()=>{
             this.loading = false
             this.success = false
             this.fail = false
           },2000)
        })
    }
  },
  components: {
    Coupon,
  }
}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



