import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    curWallet: null,
    coupons:[],//
    cities:[{text:'',value:'beijing'},{text:'',value:'shanghai'},{text:'',value:'guangzhou'},{text:'',value:'shenzhen'}],
    city:'beijing',
    
  },
  
  mutations:{
    setCurrentWallet(state, address){
      if(state.curWallet)return
      state.curWallet = address
    },
    changeCity(state,city){
      console.log('-----city:'+city)
      state.city = city || 'beijing';
    },
    searchData(state, data){
      state.coupons = data
    }
  }

})

export default store


