/**
 * 
 */
<template>
<div>
  <div :class="'coupon ' + ( status > 0 ? 'coupon-diabled':'')">
      <div class="coupon-left">
          <div class="coupon-inner">
              <div class="coupon-money">
                  <span></span>
                  <span class="sum">{{money.toFixed(2)}}</span>
              </div>
              <div class="coupon-condition">
                  <p>{{title}}</p>
              </div>
          </div>
      </div>
      <div class="coupon-right">
          <div class="coupon-inner">
              <div class="coupon-time">
                  {{author}}<br/>
                  <span v-if="status === 0" text-color="white" @click="chose">
                    
                  </span>
                  <span v-else>
                    
                  </span>
                  
              </div>
              <i class="coupon-circle top"></i>
              <i class="coupon-circle bottom"></i>
          </div>
      </div>
      <div class="coupon-light"></div>
  </div>
  <v-dialog v-model="dialog" persistent max-width="500px">
    <div v-if="working">...</div>
    <div v-else>
      <div v-if="success">15</div>
      <div v-if="fail"></div>
    </div>
  </v-dialog>
</div>
</template>

<script>
import api from '@/api'
import { mapState } from 'vuex'
export default {
  data(){
    return {
      dialog: false,
      working: false,
      success: false,
      fail: false,
    }
  },
  props: {
    title:{
      type:String,
      required:true
    },
    money:{
      type: Number,
      required:true,
    },
    author:{
      type:String,
      required: true
    },
    belong:{
      type: String,
      required: false
    },
    status: {
      type: Number,
      default: 0,
    },
    index:{
      type: Number,
      default: 0
    }
  },
  computed: {
    ...mapState(['city','curWallet'])
  },
  methods: {
    chose(){
      this.dialog = true
      let params = [this.city, index];
      api('belong', params, true)
        .then(response=>{
          this.success = true
          setTimeout(()=>{
            this.dialog = false
            this.success = false
            this.fail = false
          },5000)
        })
        .catch(err=>{
          this.fail = true
          setTimeout(()=>{
            this.dialog = false
            this.success = false
            this.fail = false
          },5000)
        });
    }
  }
}
</script>

<style scoped>
.coupon{position:relative;background:#F44336;color:#fff;width:100%;height:95px;line-height:25px;overflow:hidden;margin-top:15px;border-radius: 5px;}
.coupon-diabled{background:#c3c3c3;color:#666;}
.coupon:after{content:".";display:block;height:0;clear:both;visibility:hidden;}
.coupon-money .sum{font-size:25px;}
.coupon-left{float:left;text-align:left;width:60%;font-size:12px;}
.coupon-right{float:right;text-align: center;width:40%;font-size:14px;}
.coupon-inner{padding:10px 15px;height:95px;position: relative;}
.coupon-right .coupon-inner{border-left:2px dashed #303030;}
.coupon-money,.coupon-time{margin-top:12px;overflow: hidden;word-break: break-all;white-space: nowrap;text-overflow: ellipsis;}
.coupon-time{
  margin-top: 24px;
}
.coupon-circle{display:inline-block;width:16px;height:16px;background:#303030;position: absolute;left:-9px;border-radius:50%}
.coupon-circle.top{top:-8px;}
.coupon-circle.bottom{bottom:-8px;}
.coupon-light{position:absolute;top:20px;left:20%;height:150px;width:100%;background:rgba(255,255,255,.2);transform: rotate(-30deg);-webkit-transform: rotate(-30deg);-moz-transform: rotate(-30deg);}
</style>



