<template>
  <v-app dark>
    
    <v-toolbar
      app
      color="primary"
    >
      <v-toolbar-title class="white--text"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn flat @click="addNew">
        <v-icon>add</v-icon>
      </v-btn>
      <!-- <v-btn icon  @click="toMyCollects">
        <v-icon>grade</v-icon>
      </v-btn>
      <v-btn icon  @click="toMyCreated">
        <v-icon>card_giftcard</v-icon>
      </v-btn> -->
      <v-btn flat @click="toHome">
        <v-icon>home</v-icon>
      </v-btn>
      <v-btn flat @click="toUser">
        <v-icon>account_box</v-icon>
      </v-btn>
    </v-toolbar>
    <v-content>
      <v-alert :value="webWallentInstance===null" type="error">
        : 
        <a target="_blank" href="https://github.com/ChengOrangeJu/WebExtensionWallet">WebExtensionWallet</a> 
      </v-alert>
      <router-view/>
    </v-content>

    <v-dialog v-model="addDlgShow" persistent max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline"></span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field label="" v-model="title" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="" v-model="money" required></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
          <small v-if="addSuccess">15</small>
          <small v-if="addFail"></small>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" :loading="adding" flat @click.native="addDlgShow = false"></v-btn>
          <v-btn color="blue darken-1" :loading="adding" flat @click.native="doAdd"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
   
    <v-footer :fixed="fixed" app >
      <div style="text-align:center;width:100%;"><a href="https://nebulas.io/"></a><a
                href="https://incentive.nebulas.io/cn/signup.html?invite=Z1JDk">DAPP100NAS</a></div>
    </v-footer>
  </v-app>
</template>

<script>
import { mapState } from 'vuex'
import {callContract} from './api'

export default {
  data () {
    return {
      fixed: false,
      webWallentInstance:'',
      addDlgShow: false,
      title: null,//
      money: null,//
      adding: false,
      addSuccess: false,
      addFail: false,

    }
  },
  name: 'App',
  computed: {
    ...mapState(['curWallet','city'])
  },
  beforeMount () {
    if(webExtensionWallet === undefined){
      this.webExtensionWallet = null
    }else{
      this.webWallentInstance = webExtensionWallet
    }
    let that = this;
     window.addEventListener('message',  function(e){
       //console.log('---------message----')
       //console.log(e)
       that.readMessage(e)
     },false);
  },
  beforeDestroy () {
    let that = this;
    window.removeEventListener('message', function(e){
       //console.log('---------message----')
      // console.log(e)
       that.readMessage(e)
     }, false);
  },
  beforeUpdate () {
    this.walletInfo()
  },
  methods: {
    walletInfo(){
      if(this.curWallet)return;
        
      window.postMessage({
          "target": "contentscript",
          "data": {},
          "method": "getAccount",
      }, "*");
    },
    readMessage(e){
     // console.log('----receive message -- ' + JSON.stringify(e))
      if (e.data && e.data.data && e.data.data.account) {
        console.log('---------------account:'+ e.data.data.account);
        //
        this.$store.commit('setCurrentWallet',e.data.data.account);
      }
    },
    addNew(){
      this.addDlgShow = true
    },
    toMyCollects(){
      this.$router.push({name: 'MyCollects'})
    },
    toMyCreated(){
      this.$router.push({name: 'MyCreated'})
    },
    toHome(){
      this.$router.push('/')
    },
    toUser(){
      this.$router.push({name: 'User'})
    },
    doAdd(){
      if(this.adding)return
      this.adding = true
      this.addSuccess = false
      this.addFail = false
      // api('add', [this.city,{title: this.title, money: this.money, authro: this.curWallet}], true)
      // let data = {key: this.city, value: {title: this.title, money: Number(this.money)}};
      callContract('createCoupon',[this.city, this.title, Number(this.money)])
        .then(response=>{
          this.addSuccess = true;
          setTimeout(()=>{
            this.title = null
            this.money = null
            this.adding = false
            this.addSuccess = false
            this.addFail = false
          },5000);
        })
        .catch(err=>{
          console.error(err)
          this.addFail = true;
          setTimeout(()=>{
            this.adding = false
          },5000)
        })
      
    }


  }

}
</script>



